var formatNumber = function formatNumber(n) {
  n = n.toString();
  return n[1] ? n : '0' + n;
};

var getRemainTime = function getRemainTime(endTime) {
  var date = new Date();
  var timestamp = date.getTime();
  var t = endTime - timestamp;
  var seconds = formatNumber(Math.floor(t / 1000 % 60));
  var minutes = formatNumber(Math.floor(t / 1000 / 60 % 60));
  var hours = Math.floor(t / (1000 * 60 * 60) % 24);
  var days = Math.floor(t / (1000 * 60 * 60 * 24));
  return {
    'total': t,
    'days': days,
    'hours': hours,
    'minutes': minutes,
    'seconds': seconds
  };
};

var getCountDownTime = function getCountDownTime(endTime, timeCb, endCb) {
  var intervalTimer = function intervalTimer() {
    var remainTime = getRemainTime(endTime);
    var hours = remainTime.days * 24 + remainTime.hours;
    var time = {
      hours: hours >= 0 ? formatNumber(hours) : "00",
      minutes: remainTime.minutes >= 0 ? remainTime.minutes : "00",
      seconds: remainTime.seconds >= 0 ? remainTime.seconds : "00"
    }; //倒计时结束

    if (remainTime.total >= 0) {
      if (timeCb) timeCb(time); //setTimeout(intervalTimer,1000);
    } else {
      if (endCb) endCb();
      clearInterval(intervalEmiter);
    }
  };

  var intervalEmiter = setInterval(intervalTimer, 1000);
};

module.exports = {
  formatNumber: formatNumber,
  getRemainTime: getRemainTime,
  getCountDownTime: getCountDownTime
};